<script>
    import Vue from 'vue'
    import Router from 'vue-router'
    import HomePage from '../pages/home.vue'
    import AboutPage from '../pages/about.vue'
    import ServicePage from '../pages/service.vue'
    import DocumentPage from '../pages/document.vue'
    import ContactPage from '../pages/contact.vue'
    import JoinPage from '../pages/join.vue'
    import ArticlePage from '../pages/article.vue'
    import DocumentDetailPage from '../pages/document-detail.vue'
    Vue.use(Router)

    export default new Router({
        routes: [
            {
                path: '/',
                redirect: '/home'
            },
            {
                path: '/home',
                component: HomePage
            },
            {
                path: '/about',
                component: AboutPage
            },
            {
                path: '/service',
                component: ServicePage
            },
            {
                path: '/document',
                component: DocumentPage
            },
            {
                path: '/contact',
                component: ContactPage
            },
            {
                path: '/join',
                component: JoinPage
            },
            {
                path: '/article',
                component: ArticlePage
            },
            {
                path: '/detail',
                component: DocumentDetailPage
            }
        ]
    })
</script>