<script>
import TextDevider from '../components/text-devider.vue'
import NewsCard from '../components/news-card.vue'
export default {
    name: 'Document',
    components: {
        TextDevider,
        NewsCard
    },
    data() {
        return {
            loading: false,
            logos: []
        }
    },
    mounted: function() {
        for (let i = 0; i < 40; i++) {
            const index = i + 1;
            this.logos.push({img_url: './assets/logos/logo'+index+'.png', index: index})
        }
    },
    methods: {
        loadmore: function() {
            this.loading = true;
            setTimeout(() => {
                this.loading = false;
            }, 3000);
        }
    }
}
