<template>
    <div class="about">
        <text-devider text=""/>
        <div class="slogan">
            <div class="pic">
                <img src="../assets/slogan.png">
            </div>
        </div>
        <div class="description" style="margin-top: 700px;">
            <p></p>
            <p></p>
            <p></p>
            <p></p>
        </div>
        <text-devider text=""/>
        <div class="description">
            <p style="font-weight: bold; font-size: 30px;">4A</p>
            <p style="font-size: 20px;">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</p>
            <p style="font-weight: bold; font-size: 30px;">&nbsp;&nbsp;&nbsp;&nbsp;</p>
            <p style="font-size: 24px;">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</p>
            <p style="font-size: 24px;"></p>
            <p style="font-size: 16px;"></p>
            <p style="font-weight: bold;font-size: 20px;">... ...</p>
        </div>
        <text-devider text=""/>  
        <div class="charity">
            <img src="../assets/charity.png" alt="">
        </div>
        <div class="description">
            <p></p>
            <p></p>
        </div>
        <div style="padding-top: 60px;">
            <div v-for="charity in charities" :key="charity.id" style="margin-bottom: 30px;">
            <news-card bg-color="#eaeaea" :source="charity"/>
        </div>
        </div>
        <text-devider text=""/> 
        <div v-for="viewpoint in viewpoints" :key="viewpoint.id" style="margin-bottom: 20px;">
            <news-card :source="viewpoint"/>
        </div>
    </div>
</template>
<script>
import TextDevider from '../components/text-devider.vue'
import NewsCard from '../components/news-card.vue'
export default {
    name: 'About',
    components: {
        TextDevider,
        NewsCard
    },
    data() {
        return {
            charities: [],
            viewpoints: []
        };
    },
    mounted: function() {
        this.getCharities();
        this.getViewpoints();
    },
    methods: {
        getCharities: function() {
            this.$http.get("http://60.205.191.12:8081/api/articles", {params: { type: 1 } }).then(response => {
                const result = JSON.parse(response.bodyText);
                this.charities = result.data[0].list;
            });
        },
        getViewpoints: function() {
            this.$http.get("http://60.205.191.12:8081/api/articles", { params: { type: 2 } }).then(response => {
                const result = JSON.parse(response.bodyText);
                this.viewpoints = result.data[0].list;
            });
        }
    }
}
</script>
<style>
.slogan {
    position: absolute;
    left: 0;
    background: #B1B1B2;
    width: 100%;
    text-align: center;
}
.slogan .pic{
    width: 1280px;
    padding: 2px 0;
    height: 600px;
    margin: 0 auto;
}
.slogan .pic img {
    width: 100%;
    height: 100%;
}
.charity {
    text-align: center;
}
</style>