<template>
    <div class="join">
        <text-devider text=""/>
        <div class="pic">
            <img src="../assets/joinus.png" alt="">
        </div>
        <div class="description">
            <p></p>
            <p></p>
        </div>
        <text-devider text=""/>
        <div class="company-list">
            <div class="company-list-item" v-for="company in companys" :key="company.id">
                <div class="title">
                    {{company.name}}
                </div>
                <div class="jobs">
                    <el-collapse v-model="activeNames" accordion v-if="company.jobs.length > 0">
                        <el-collapse-item name="1" v-for="job in company.jobs" :key="job.id">
                            <template slot="title">
                            {{job.title}}
                            </template>
                            <div v-html="job.detail"></div>
                        </el-collapse-item>
                    </el-collapse>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import TextDevider from "../components/text-devider.vue";
import NewsCard from "../components/news-card.vue";
export default {
    name: "Join",
    components: {
        TextDevider,
        NewsCard
    },
    data() {
        return {
            companys: []
        };
    },
    mounted: function() {
        this.getJobs();
    },
    methods: {
        getJobs: function() {
            this.$http
                .get("http://60.205.191.12:8081/api/jobs", { params: { count: 2 } })
                .then(response => {
                    const result = JSON.parse(response.bodyText);
                    this.companys = result.data[0].list;
                });
        }
    }
};
</script>
<style>
.el-collapse {
  border: none;
}
.el-collapse .el-icon-arrow-right {
  font-size: 16px;
}
.el-collapse .el-icon-arrow-right:before {
  content: "";
  color: #333333;
}
.el-collapse-item__arrow.is-active {
  transform: none;
}
.el-collapse-item__header {
  width: 50%;
  font-size: 24px;
  font-weight: bold;
  border-color: #BEBFBE;
}
.el-collapse-item__content {
  background: #e3e3e3;
  padding: 30px;
}
.join .company-list-item .title {
  display: inline-block;
  padding: 10px 20px;
  margin: 30px 0;
  background: #c4c5c5;
  font-size: 26px;
  font-weight: bold;
}
.join .pic img {
    width: 100%;
}
</style>