<template>
    <div class="newscard" v-bind:style="{'background-color': bgColor}">
        <el-row>
            <el-col :span="6">
                <img src="data.img_url">
            </el-col>
            <el-col :span="18">
                <div class="news-info">
                    <div class="title" :style="{'font-weight': data.sub_title ? 'normal' : 'bold', 'font-size': data.sub_title ? '16px' : '18px'}">
                        <router-link :to="{ path: '/article', params: { id: data.id }}" v-if="!noLink">{{data.title}}</router-link>
                        <span v-if="noLink">{{data.title}}</span>
                    </div>
                    <div class="sub-title" v-if="data.sub_title">{{data.sub_title}}</div>
                    <div class="desc">{{data.desc}}</div>
                    <div class="footer-info">
                        <span v-if="data.updated_at || data.from">{{data.updated_at | moment('YYYY-MM-DD')}}{{'' + data.from}}</span>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
  name: "NewsCard",
  props: {
      bgColor: {
          type: String,
          default: 'inherit'
      },
      source: {
          type: Object,
          default: function() {
              return { 
                title: null,
                desc: null,
                detail: null,
                img_url: null,
                update_time: null,
                from: null
              }
          }
      },
      noLink: {
          type: Boolean,
          default: false
      }
  }
};
</script>
<style>
.newscard {
    padding: 30px;
}
.newscard .title{
    font-size: 16px;
    font-weight: bold;
    width: 518px;
    overflow: hidden;
    text-overflow: ellipsis;
    word-wrap: none;
    border-bottom: 1px solid #333;
    padding: 6px 0 14px;
}
.newscard .sub-title{
    font-size: 18px;
    font-weight: bold;
    padding-top: 14px;
}
.newscard .desc{
    padding: 14px 0;
}
.newscard .title a:hover {
  text-decoration: underline;
}
</style>
