<template>
    <div class="contact">
        <text-devider text=""/>
        <div class="description">
            <p></p>
            <p></p>
        </div>
        <div class="contact-info">
            <div class="message">
                <h3 class="title">
                    
                </h3>
                <div class="contact-way">
                    <div class="title">
                        
                    </div>
                    <div class="content">
                        716
                    </div>
                </div>
                <div class="contact-way">
                    <div class="title">
                        
                    </div>
                    <div class="content">
                        <div>0755-8377 7255</div>
                        <div>0852-2369 1861</div>
                    </div>
                </div>
                <div class="contact-way">
                    <div class="title">
                        
                    </div>
                    <div class="content">
                        lz@lingzhen.com.cn
                    </div>
                </div>
            </div>
            <div id="maps"></div>
            <div class="clearFix"></div>
        </div>
    </div>
</template>
<script>
import TextDevider from '../components/text-devider.vue'
import NewsCard from '../components/news-card.vue'
import BMap from '../baidu-map.js'

export default {
    name: 'Contact',
    components: {
        TextDevider,
        NewsCard
    },
    methods: {
    },
    mounted: function() {
        BMap.init().then((BMap) => {
            const map = new BMap.Map('maps');
            const point = new BMap.Point(114.049333, 22.555316);
            map.centerAndZoom(point, 15);  // 
            const marker = new BMap.Marker(point);
            map.addOverlay(marker);  // 
            const sContent = "<h4 style='margin:0 0 5px 0;padding:0.2em 0'></h4>" + 
            "<p style='margin:0;line-height:1.5;font-size:13px;'>716</p>" + 
            "</div>";
            const infoWindow = new BMap.InfoWindow(sContent);  // 
            marker.addEventListener("click", function(){          
                this.openInfoWindow(infoWindow);
            });
            const top_left_control = new BMap.ScaleControl({anchor: BMAP_ANCHOR_TOP_LEFT});  // 
            const top_left_navigation = new BMap.NavigationControl();   // 
            const overView = new BMap.OverviewMapControl();   // 
            const overViewOpen = new BMap.OverviewMapControl({isOpen:true, anchor: BMAP_ANCHOR_BOTTOM_RIGHT});  // 
            map.addControl(top_left_control);
            map.addControl(top_left_navigation);
            map.addControl(overView);
            map.addControl(overViewOpen);
        })
    }
}
</script>
<style>
.clearFix {
    clear: both;
}
.contact-info {
    padding: 0 38px;
    padding-top: 40px;
}
.contact-info .message {
    float: left;
}
.contact-info #maps {
    float: right;
}
.contact-info .message h3.title {
    font-weight: normal;
    border-bottom: 1px solid #C4C5C5;
    font-size: 16px;
    color: #595757;
    padding: 14px 40px 14px 0;
    margin: 0;
}
.contact-way {
    margin: 6px 0 28px 6px;
}
.contact-way .title {
    font-weight: bold;
    font-size: 20px;
    padding: 6px 0;
}
.contact-way .content {
    font-size: 18px;
}
#maps {
    width: 650px;
    height: 350px;
}
</style>