import Vue from 'vue'
import App from './App.vue'
import {Row, Col, Carousel, CarouselItem, Collapse, CollapseItem, Loading} from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import router from './router/index'
import VueResource from 'vue-resource'
import VueMoment from 'vue-moment'


Vue.config.productionTip = false
Vue.use(Row)
Vue.use(Col)
Vue.use(Carousel)
Vue.use(CarouselItem)
Vue.use(Collapse)
Vue.use(CollapseItem)
Vue.use(Loading)
Vue.use(VueResource)
Vue.use(VueMoment)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
