<template>
    <div class="document-detail">
        <text-devider text=""/>
        <div class="pic">
            <img src="../assets/case_images/1.jpg">
        </div>
        <text-devider text=""/>
        <div v-for="news in newsList" :key="news.id" class="news-item">
            <news-card :source="news"/>
        </div>
    </div>
</template>
<script>
import TextDevider from '../components/text-devider.vue'
import NewsCard from '../components/news-card.vue'
export default {
    name: 'documentDetail',
    components: {
        TextDevider,
        NewsCard
    },
    data() {
        return {
            newsList: []
        }
    },
    mounted: function() {
        this.getNews();
    },
    methods: {
        getNews: function() {
            return this.$http
                .get("http://60.205.191.12:8081/api/articles", { params: { type: 3 } })
                .then(response => {
                    const result = JSON.parse(response.bodyText);
                    this.newsList = result.data[0].list;
                });
        }
    }
}
</script>
<style>
.document-detail .pic{
    width: 100%;
    height: 312px;
    overflow: hidden;
    padding: 30px 0;
}
.document-detail .pic img {
    width: 100%;
}
</style>