<template>
    <div class="devider">
        <div class="line"><span></span></div>
        <div class="text">{{text}}</div>
        <div class="line"><span></span></div>
    </div>
</template>
<script>
    export default {
        name: 'TextDevider',
        props: {
            text: String
        }
    }
</script>
<style>
    .devider {
        display: table;
        width: 100%;
        padding: 30px 0;
    }
    .devider .line, .devider .text{
        display: table-cell;
        vertical-align: middle;
    }
    .line {
        line-height: 28px;
        padding: 0 38px;
    }
    .line>span{
        display: inline-block;
        height: 2px;
        background: #C4C5C5;
        width: 100%;
    }
    .text {
        font-size: 28px;
        font-weight: 700;
        text-align: center;
        width: 220px;
    }
</style>